:root {
  --body-bg: #123;
  --nav-bg: rgb(69, 92, 115);
  --border-radius: 1rem;
}

* {
  box-sizing: border-box;
}

html {
  font-family: system-ui;
}

body {
  min-height: 100vh;
  margin: 0;
  font-size: 1.25rem;
  background: var(--body-bg);
  color: white;
  display: grid;
  grid-template-columns: 20rem 1fr;
  gap: 4rem;
}

nav {
  position: sticky;
  top: 0;
  max-height: 100svh;
  background-color: var(--nav-bg);
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 2rem;
  padding-block: 2rem;
  font-size: 2rem;
}

.nav-list li {
  padding: 1rem 2rem;
  margin-left: 2rem;
  border-radius: 100vw 0 0 100vw;
}

.nav-list li.active::before,
.nav-list li.active::after {
  content: "";
  position: absolute;
  width: var(--border-radius);
  height: var(--border-radius);
  right: 0;
  background: var(--nav-bg);
}

.nav-list li.active::before {
  top: calc(var(--border-radius) * -1);
  border-radius: 0 0 100vw 0;
  box-shadow: 10px 10px 0 10px var(--body-bg);
}

.nav-list li.active::after {
  bottom: calc(var(--border-radius) * -1);
  border-radius: 0 100vw 0 0;
  box-shadow: 10px -10px 0 10px var(--body-bg);
}

.nav-list li.active {
  view-transition-name: activeNavTab;
  position: relative;
  background: var(--body-bg);
}

.nav-list li:nth-child(1) a {
  view-transition-name: navlink-1;
}

.nav-list li:nth-child(2) a {
  view-transition-name: navlink-2;
}

.nav-list li:nth-child(3) a {
  view-transition-name: navlink-3;
}

.nav-list li:nth-child(4) a {
  view-transition-name: navlink-4;
}

.nav-list li:nth-child(5) a {
  view-transition-name: navlink-5;
}

.nav-list li:nth-child(6) a {
  view-transition-name: navlink-6;
}

::view-transition-group(activeNavTab) {
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
}

.nav-list a {
  color: #fff;
  text-decoration: none;
  position: relative;
  z-index: 10;
}